import header from '@/components/header'
export default {
  data () {
    return {
      headtitle: '设置',
      settingArr: [
        {
          id: 'changePassword',
          name: '修改密码'
        },
        {
          id: 'logout',
          name: '退出当前账号'
        }
      ]
    }
  },

  components: {
    'v-header': header
  },

  methods: {
    logout () {
      this.$store.dispatch('loginout', this)
    }
  }
}




